
.route-index {
  margin-top: 0 !important;

  .l-main-header {
    display: none;
    position: relative;
    z-index: 3;
  }

  .l-hero {
    position: relative;

    @include media-breakpoint-up(lg) {
      height: 100vh;
    }

    .c-banner-arrow {
      position: absolute;
      left: 50%;
      bottom: 1.5rem;
      transform: translateY(-50%);
    }
  }

  .browser-window {
    border-radius: 7px;
    overflow: hidden;

    .top-bar {
      background-color: darken($primary, 10%);
      height: 27px;
      position: relative;
    }

    .circles {
      display: flex;
      position: absolute;
      top: 8px;
      left: 10px;
    }

    .circle {
      width: 10px;
      height: 10px;
      background-color: #ccc;
      border-radius: 10px;

      &--red {
        background-color: $danger;
      }

      &--yellow {
        background-color: $warning;
      }

      &--green {
        background-color: $green-500;
      }
    }

    .content {
      border: 1px solid rgba($dark, .5);

      pre {
        margin: 0;
        font-size: 13px !important;
      }
    }
  }
}

